import React from 'react'
const PageTwo = ({ form, onSubmit, onChange }) => {
  return (
    <div className="PageTwo">
      <h2></h2>
      <form onSubmit={onSubmit}>
        <label htmlFor="yourname">yourname</label>
        <input name="name" value={form.name} onChange={onChange} placeholder=" " />
        <br />
        <label htmlFor="nickname">nickname</label>
        <input name="nickname" value={form.nickname} onChange={onChange} placeholder=" " />
        <br />
        <label htmlFor="email">email</label>
        <input name="email" value={form.email} onChange={onChange} placeholder=" " />
        <br />
        <button type="submit"></button>
      </form>
    </div>
  )
}

export default PageTwo
